<template>
  <div>
    <div> 
      <input v-model="inputVulue"/>
      <button @click="handleSubmit">提交</button>
    </div>
    <ul>
      <todo-item v-for="(item,index) of list"
      :content="item"
      :index="index"
      @delete="handledelete"
      ></todo-item>
    </ul>
  </div>
</template>

<script>
  import Todoitem from './components/Todoitem'
  export default {
    components:{
      'todo-item':Todoitem
    },
    data(){
      return {
        inputVulue:'',
        list:[],
      }
    },
    methods:{
      handleSubmit(){
        this.list.push(this.inputVulue)
        this.inputVulue =''
      },
      handledelete(index){
        this.list.splice(index,1)
      }
    }
  }

</script>

<style>

</style>



