<template>
  <li @click="handledelete">{{content}}</li>
</template>

<script>
  export default{
    props:['content','index'],
    methods:{
      handledelete(){
        this.$emit('delete',this.index)
      }
    }
  }
</script>

<style scoped>

</style>



